@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "GenSenRounded";
  src: url("/src/fonts/GenSenRounded.woff");
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "GenSenRounded";
}

.at-top {
  color: rgb(8 51 68);
  background-color: transparent;
  transition: 0.3s;
}

.not-at-top {
  color: #fff;
  background-color: rgb(8 51 68);
  transition: 0.3s;
}

a.active {
  color: rgb(252 211 77);
  border-bottom: 2px solid rgb(252 211 77);
  transition: 0.2s;
}

a:hover {
  color: rgb(252 211 77);
  transition: 0.1s;
}

.hide {
  opacity: 0;
  filter: blur(5px);
  transform: translateX(-100%);
  transition: all 0.8s;
}

.show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
}

.polygon-in {
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
}

.polygon-out {
  clip-path: polygon(0 0, 100% 20%, 100% 100%, 0 100%);
}
